<style lang="less">
    @import '../../../less/lib/mixins.less';
    .page-authStep3{
        .itemTitle{
            border-bottom:1px solid #b2b2b2;
            .rem(border-bottom-width, 2);
            .rem(padding, 0, 0, 26);
            .rem(margin-bottom, 10);
        }
        .block{
            background:#fff;
            .rem(padding, 26, 16);
            .rem(margin, 0, 0, 26, 0);
            position:relative;
            
            .header{
                .rem(font-size, 30);
            }
            
            textarea{
                width:100%;
                border:0 none;
                .rem(font-size, 30);
            }

            .mapWrap{
                .map{
                    background:url(../../../img/mapImg.png) no-repeat;
                    .rem(background-size, 750, 118);
                    .rem(height, 118);
                    .border-radius(8);
                    position: relative;
                    color:#333;
                    .rem(margin-right, 140);
                    .ico{
                        display: inline-block;
                        color:#54c5ff;
                        .rem(width, 70);
                        .rem(font-size, 40);
                        text-align: center;
                        .rem(height, 118);
                        .rem(line-height, 118);
                        position: absolute;
                        top:0;
                        left:0;
                    }
                    div{
                        .rem(height, 118);
                        .rem(padding, 10, 10, 10, 70);
                        vertical-align: middle;
                        display: table-cell;
                        overflow: hidden;
                        .rem(font-size, 24);
                    }
                }

                .icoWrap{
                    display: table;
                    position: absolute;
                    .rem(top, 30);
                    .rem(right, 16);
                    .rem(width, 120);
                    .rem(height, 150);
                    border-left:1px solid #dedede;
                    .rem(padding, 30, 0, 30, 20);
                    .rem(border-left-width, 2);
                    text-align: center;
                    .rem(font-size, 24);
                    div{
                        display: table-row;
                    }
                    .ico{
                        display: table-row;
                        .rem(font-size, 60);
                        color:#11cd6e;
                    }
                }
            }
        }
    }
</style>

<template>
    <div class="page-authStep1 page-authStep3">
        <header-bar :title="title" :back="true"></header-bar>
        <div class="content showHeader" :class="{showFooter: (formData.sceneIds && formData.timeConf && formData.longitude)}">
            <div class="stepTitle"></div>
            <div class="ui-floatCenter">
                <div class="ui-sl-floatCenter">
                    <i class="ui-floatCenter-item icon icon-anquanbaozhang"></i>
                    <i class="ui-floatCenter-item icon link"></i>
                    <i class="ui-floatCenter-item icon icon-anquanbaozhang icon-anquanbaozhang2"></i>
                </div>
			</div>
            <div class="stepText">!</div>
            <div class="progress">
                <span class="item"><span>1</span></span>
                <span class="item"><span>2</span></span>
                <span class="item cur"><span>3</span></span>
            </div>
            <div class="params">
                <div class="block">
                    <div class="itemTitle">()</div>
                    <scene-type 
                        :scene-list="indexData.sceneList"
                        :scene-ids.sync="formData.sceneIds"
                    ></scene-type>
                </div>
                <div class="block">
                    <div class="header">()</div>
                    <div class="mapWrap">
                        <div class="map">
                            <i class="ico ico-zuobiao"></i>
                            <div>{{formData.workplace}}</div>
                        </div>
                        <div class="icoWrap" @click="getLocate">
                            <i class="ico ico-locate"></i>
                            <div></div>
                        </div>
                    </div>
                </div>
                <div class="block">
                    <div class="itemTitle">( </div>
                    <time-conf :timer.sync="formData.timeConf"></time-conf>
                </div>
            </div>
        </div>
        <span 
            class="ui-btn ui-btn-big"
            v-if="(formData.sceneIds && formData.timeConf && formData.longitude)"
            @click="submit"
        >
            
        </span>

        <toast :show.sync="isShowToast" :time="1000">{{toastText}}</toast>
        <loading :show="isShowloading" :text="loadingText"></loading>

    </div>
</template>

<script>
export default {
    data() {
        return {
            indexData: indexData,
            title: '',
            isShowToast: false,
            toastText: ',',
            isShowloading: false,
            loadingText: ',!',
            formData: {
                sceneIds: null,
                timeConf: null,
                longitude: null,
                workplace: null
            }
        }
    },
    route: {
        data (transition){
            let self     = this,
                query    = transition.to.query;
                
            $.extend(self.formData, query);
        }
    },
    methods: {
        getLocate(){
            let self = this;
            self.isShowloading = true;
            AMap.service(["AMap.Geolocation"], function() {

                var geolocation = new AMap.Geolocation({
                    enableHighAccuracy: true,//:true
                    timeout: 10000,          //10
                    buttonPosition:'RB'
                });

                geolocation.getCurrentPosition();

                AMap.event.addListener(geolocation, 'complete', (data)=>{
                    AMap.service(["AMap.Geocoder"], function() { //
                        let geocoder  = new AMap.Geocoder({
                                radius: 1000,
                                extensions: "all"
                            }),
                            longitude = data.position.getLng(),
                            latitude  = data.position.getLat();

                        geocoder.getAddress([longitude, latitude], function(status, result) {
                            if (status === 'complete' && result.info === 'OK') {
                                console.log(result.regeocode.formattedAddress)
                                self.formData.longitude = longitude;
                                self.formData.latitude  = latitude;
                                self.formData.workplace = result.regeocode.formattedAddress;
                                self.isShowloading      = false;
                            }
                        });
                    });
                });

                AMap.event.addListener(geolocation, 'error', (data)=>{
                    self.isShowToast = true;
                });
            });
        },
        submit(){
            let self = this;
            self.isShowloading = true;
            $.ajax({
                url: "/soytime/ca/save",
                type:'POST',
                dataType: 'json',
                data: self.formData,
                success: (data)=>{
                    if( data.success ){
                        self.isShowloading = false;
                        self.$route.router.go('/auth/checking');
                    }
                },
                error: ()=>{
                    self.isShowToast = true;
                    self.isShowloading = false;
                }
            });
        }
    },
    components: {
        'timeConf': require('../../../components/timeConf.vue'),
        'sceneType': require('../../../components/sceneType.vue'),
        'headerBar': require('../../../components/header.vue'),
        'toast': require('../../../components/toast'),
        'loading': require('../../../components/loading')
    }
}
</script>



